<template>
  <div class="container userform">
    <main class="form-signin w-100 m-auto mt-5">
      <h3 class="text-center mb-3 fw-normal"></h3>
      <form v-on:submit="onSubmit" @submit.prevent="submitForm" id="myForm">
        <div class="mt-4">
          <div class="form-check form-check-inline">
            <input v-model="isManager" class="form-check-input" type="radio" name="isManager" id="inlineRadio1" value="N">
            <label class="form-check-label" for="inlineRadio1"></label>
          </div>
          <div class="form-check form-check-inline">
            <input v-model="isManager" class="form-check-input" type="radio" name="isManager" id="inlineRadio2" value="Y">
            <label class="form-check-label" for="inlineRadio2"></label>
          </div>
        </div>
        <div class="form-floating">
          <input v-on:input="onIdInput" v-model="id" v-bind:class="{ 'is-valid': isIdValid, 'is-invalid': !isIdValid && isIdDirty }" type="text" class="form-control" name="id" id="floatingId" placeholder="" />
          <label for="floatingId"></label>
          <small class="form-text text-muted">   5~10  </small>
          <div class="valid-feedback">   .</div>
          <div class="invalid-feedback">    .</div>
        </div>
        <div class="form-floating">
          <input v-on:input="onPwdInput" v-model="pwd" v-bind:class="{'is-valid':isPwdValid, 'is-invalid':!isPwdValid && isPwdDirty}" type="password" class="form-control" name="pwd" id="floatingPwd" placeholder="" />
          <label for="floatingPassword"></label>
          <small class="form-text text-muted">    .</small>
          <div class="invalid-feedback">   </div>
        </div>
        <div class="form-floating">
          <input v-on:input="onPwdInput" v-model="pwd2" type="password" class="form-control" name="pwd2" id="floatingPwd2" placeholder=" " />
          <label for="floatingPassword2"> </label>
        </div>
        <div class="form-floating">
          <input type="text" v-model="phone" class="form-control" name="phone" id="floatingPhone" placeholder=" " />
          <label for="floatingPhone"> </label>
        </div>
        <div class="form-floating">
          <input type="date" v-model="birth" class="form-control" name="birth" id="floatingBirth" placeholder="" />
          <label for="floatingBirth"></label>
        </div>
        <div class="form-floating">
          <input v-on:input="onEmailInput" v-model="email" v-bind:class="{'is-valid':isEmailValid, 'is-invalid':!isEmailValid && isEmailDirty}" type="email" class="form-control" name="email" id="floatingEmail" placeholder="" />
          <label for="floatingEmail"></label>
          <div class="invalid-feedback email-feedbak">   .</div>
        </div>
        <button style="background:#1EBD8E; color:#fff" v-bind:disabled="!isFormValid" class="w-100 button btn btn-lg mt-3" type="submit"></button>
      </form>
    </main>
  </div>
</template>
  
<script>
import axios from 'axios';
export default {
  name: 'Signup',
  data() {
    return {
      isIdValid: false,
      isPwdValid: false,
      isEmailValid: false,
      isManager:'',
      id: "",
      email: "",
      pwd: "",
      pwd2: "",
      phone:"",
      birth:"",
      isIdDirty: false,
      isEmailDirty: false,
      isPwdDirty: false,
    }
  },
  computed: {
    isFormValid() {
      //      .
      let result = this.isIdValid && this.isPwdValid && this.isEmailValid 
                && (this.isManager != "") && (this.phone != "")  && (this.birth != "");
      //      .
      return result;
    }
  },
  methods: {
    onIdInput() {
      this.isIdDirty = true;
      const reg = /^[a-z].{4,9}$/;
      const isMatch = reg.test(this.id);
      if (!isMatch) {
        this.isIdValid = false;
        return;
      }

      const self = this;
      const url = '/plec/api/users/checkid';
      const data = { lecUserId: this.id }
      axios.get(url, { params: data })
        .then(function (response) {
          console.log(response);
          if (response.data.body.isValid) {
            self.isIdValid = true;
          } else {
            self.isIdValid = false;
          }
        })
    },
    onPwdInput() {
      this.isPwdDirty = true;
      const reg = /[\W]/;
      if (!reg.test(this.pwd)) {
        this.isPwdValid = false;
        return;
      }

      if (this.pwd == this.pwd2) {
        this.isPwdValid = true;
      } else {
        this.isPwdValid = false;
      }
    },

    onEmailInput() {
      this.isEmailDirty = true;

      const reg = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
      if (!reg.test(this.email)) {
        this.isEmailValid = false;
      } else {
        this.isEmailValid = true;
      }
    },
    onSubmit(e) {
      if (!this.isFormValid) {
        e.preventDefault();
      } 
    },
    submitForm(){
      const url = '/plec/api/users';
      const data = {
        lecUserId: this.id,
        managerYn: this.isManager,
        userBirth: this.birth,
        userEmail: this.email,
        userPhone: this.phone, 
        userPwd : this.pwd
      }
      var vm = this;
      axios.post(url, data)
        .then(function (response) {
          console.log(response);
          alert('!  !');
          vm.$router.push('/login');
        })
    }
  }
}
</script>
<style>
@import '../../assets/css/user.css'
</style>